.landingText{
    font-family: 'Roboto', sans-serif;
    font-size: 70px !important;
    padding-top: 25%
}
.hero{   
    background-image: url(../features/img/websitebackground.png) ;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}
.heroPic{
    margin-left:30%;
    margin-right:35%;
}

.landingButtonDiv{
    font-family: 'Roboto', sans-serif;
    color: #c64c4e;
    font-size: 30px !important;

}

.heroContainer{
    padding-bottom: 5rem;
}


